    { id: 1, name: 'Apple iPhone 14 Pro Max', description: 'A16仿生芯片，超视网膜XDR显示屏', price: 8999.00, originalPrice: 9999.00, discount: 9, image: 'https://picsum.photos/300/300?random=20', sales: 5420, rating: 4.5, reviews: 1245, tags: ['爆款', '新品'], category: 1 },
    { id: 2, name: '华为MateBook X Pro', description: '13代酷睿处理器，3.1K全面屏', price: 8999.00, image: 'https://picsum.photos/300/300?random=21', sales: 3210, rating: 4.8, reviews: 876, tags: ['轻薄', '高性能'], category: 2 },
    { id: 3, name: '戴森吹风机 HD15', description: '负离子护发，智能温控技术', price: 3190.00, originalPrice: 3490.00, discount: 9.1, image: 'https://picsum.photos/300/300?random=22', sales: 1987, rating: 4.7, reviews: 542, tags: ['热卖'], category: 3 },
    { id: 4, name: 'SK-II神仙水护肤套装', description: '经典护肤精华露230ml', price: 1540.00, image: 'https://picsum.photos/300/300?random=23', sales: 4210, rating: 4.9, reviews: 2103, tags: ['进口', '礼盒'], category: 4 },
    { id: 5, name: 'Nike Air Jordan 1', description: '经典高帮篮球鞋', price: 1299.00, originalPrice: 1499.00, discount: 8.7, image: 'https://picsum.photos/300/300?random=24', sales: 2987, rating: 4.6, reviews: 987, tags: ['限量', '潮鞋'], category: 5 },
    { id: 6, name: '索尼 PlayStation 5', description: '光驱版游戏主机', price: 3899.00, image: 'https://picsum.photos/300/300?random=25', sales: 5123, rating: 4.8, reviews: 1567, tags: ['新品', '游戏'], category: 10 },
