    { id: 8, name: '戴森空气净化器', description: '除甲醛除菌，智能净化', price: 5990.00, image: 'https://picsum.photos/300/300?random=27', sales: 1567, rating: 4.7, reviews: 543, tags: ['智能', '热卖'], category: 3 }
  ])
  
  // 限时抢购商品
  const flashSaleProducts = ref([
    { id: 9, name: '小米13 Ultra', description: '徕卡光学镜头', price: 5999.00, originalPrice: 6499.00, image: 'https://picsum.photos/300/300?random=28', salePercent: 78 },
    { id: 10, name: 'iPad Pro 2023', description: 'M2芯片，120Hz刷新率', price: 6799.00, originalPrice: 7299.00, image: 'https://picsum.photos/300/300?random=29', salePercent: 65 },
    { id: 11, name: 'Bose QuietComfort 45', description: '主动降噪耳机', price: 1999.00, originalPrice: 2299.00, image: 'https://picsum.photos/300/300?random=30', salePercent: 42 },
