      links: ['货到付款', '在线支付', '分期付款', '公司转账']
    },
    {
      title: '售后服务',
      links: ['售后政策', '价格保护', '退款说明', '返修/退换货']
    }
  ])
  
  // 计算属性：获取当前分类的子分类
  const getSubcategories = computed(() => {
    if (!currentCategory.value) return []
    return subcategories.value.filter(sub => sub.parentId === currentCategory.value)
  })
  
  // 计算属性：获取过滤后的商品
  const filteredProducts = computed(() => {
    if (currentProductTab.value === 0) {
      return products.value
    }
    // 简单模拟按分类筛选
    return products.value.slice(0, 6)
  })
  
  // 轮播图自动切换定时器
  let bannerTimer = null
  
  // 方法：切换用户菜单显示状态
  const toggleUserMenu = () => {
    showUserMenu.value = !showUserMenu.value
  }
  
  // 方法：选择子分类
  const selectSubcategory = (id) => {
    alert(`打开子分类: ${id}`)
    showCategoryMenu.value = false
    currentCategory.value = null
  }
  
  // 方法：添加商品到购物车
  const addToCart = (product) => {
    cartCount.value++
    alert(`已添加 "${product.name}" 到购物车`)
  }
  
  // 方法：查看商品详情
  const viewProduct = (id) => {
    alert(`查看商品详情: ${id}`)
  }
  
  // 方法：查看购物车
  const viewCart = () => {
    alert('查看购物车')
  }
  
  // 方法：查看订单
  const viewOrders = () => {
    alert('查看我的订单')
  }
  
  // 方法：搜索商品
  const searchProducts = () => {
    if (searchKeyword.value.trim()) {
      alert(`搜索: ${searchKeyword.value}`)
    }
  }
  
  // 方法：用户登出
  const logout = () => {
    alert('用户已登出')
    showUserMenu.value = false
  }
  
  // 方法：返回首页
  const goHome = () => {
    currentNav.value = 0
    currentProductTab.value = 0
    window.scrollTo(0, 0)
  }
  
  // 倒计时更新
  const updateCountdown = () => {
    // 这里简化处理，实际应计算真实时间差
    const seconds = parseInt(countdown.value.seconds) - 1
    if (seconds < 0) {
      countdown.value.seconds = 59
      const minutes = parseInt(countdown.value.minutes) - 1
      if (minutes < 0) {
        countdown.value.minutes = 59
        countdown.value.hours = parseInt(countdown.value.hours) - 1
      } else {
        countdown.value.minutes = minutes < 10 ? `0${minutes}` : `${minutes}`
      }
    } else {
      countdown.value.seconds = seconds < 10 ? `0${seconds}` : `${seconds}`
    }
  }
  
  // 组件挂载时启动定时器
  onMounted(() => {
    // 轮播图自动切换
    bannerTimer = setInterval(() => {
      currentBanner.value = (currentBanner.value + 1) % banners.value.length
    }, 5000)
    
    // 倒计时更新
    setInterval(updateCountdown, 1000)
  })
  
  // 组件卸载前清除定时器
  onBeforeUnmount(() => {
    if (bannerTimer) {
      clearInterval(bannerTimer)
    }
  })
  </script>
  
